<template>
  <div>{{ data }}</div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: ["id", "number"],
  data() {
    return {
      data: [],
    };
  },

  computed: {
    ...mapState(["hoteldata", "moteldata", "villadata"]),
  },
  created() {
    if(this.id == 0){
        this.data = this.moteldata[this.number];
    }
    else if (this.id == 1) {
      this.data = this.hoteldata[this.number];
    } else if (this.id == 2) {
      this.showVilla = true;
      this.data = this.villadata[this.number];
    }
  },
};
</script>

