<template>
  <div class="content mt-5">
    <div class="item-list pb-2 d-flex justify-content-between">
      <div class="fs-5">지역 추천</div>
      <div class="fs-6">광고</div>
    </div>

    <div
      v-for="(data, index) in moteldata"
      :key="index"
      class="item-list d-flex flex-column"
    >
    <router-link :to="`/badchoicepo/product/detail/0/${index}`" :params="`{ id: ${index} }`">
      <div class="item border">
        <img  style="background-color: rgba(0, 0, 0, 0.5);" class="image-thumbnail" :src="data.thumbnailImage" />
        <div class="dark-overlay"></div> <!-- 어두운 효과를 주는 요소 -->
        <div class="evt-info">
          <span
            v-for="(service, index) in data.amenities"
            :key="index"
            class="pt-2 text-white"
            :style="{
              'margin-left':
                index === 0 ? '10px' : '0' /* 맨 앞에만 margin-left 추가 */,
              'margin-right':
                index === data.amenities.length - 1
                  ? '10px'
                  : '0' /* 맨 뒤에만 margin-right 추가 */,
            }"
          >
            {{ service }}
          </span>
        </div>
        <div class="text-start name text-white">
          <p class="fs-4 fw-bold">
            {{ data.name }}
          </p>
          <p class="fs-4">
            {{ data.location }}
          </p>
        </div>
        <div class="price text-end fs-5 text-white">
          <p class="fw-bold">
            대실 {{ formatNumber(data.hourlyRate[0]) }} <br />
            숙박 {{ formatNumber(data.nightlyRate[0]) }}
          </p>
        </div>
      </div>
    </router-link>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["moteldata"]),
      
  },
  data(){
    return{
      roomList : this.moteldata
    }
  },
  
  methods: {
    formatNumber(number) {
      // 숫자를 문자열로 변환
      const numberString = number.toString();

      // 숫자를 ','를 기준으로 분할
      const parts = numberString.split(".");

      // 정수 부분에 ',' 추가
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");

      // 다시 합치기
      return parts.join(".");
    },
  },
};
</script>

